___CSS_LOADER_EXPORT___.push([module.id, `.tab-close {
    color: rgb(128, 128, 128);
    text-align: right;
    cursor: pointer;
}
.nav-tabs{
    border-bottom:none;
}

a{
    text-decoration: none;
}
.tab-container{
    margin: 0;
    padding: 0;
    max-height: 35px;
    max-width: 100%;
    overflow: hidden;
    position: relative;
}
ul.tabs{
    margin: 0;
    list-style-type : none;
    line-height : 35px;
    max-height: 35px;
    padding-right: 20px
}

ul.tabs > li.active{
    z-index: 2;
    background: #f5f5f5;
}

ul.tabs > li{
    float : right;
    margin : 5px -10px 0;
    border-top-right-radius: 25px 170px;
    border-top-left-radius: 20px 90px;
    padding : 0 30px 0 25px;
    height: 170px;
    background: #ddd;
    position : relative;
}

.tab-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;

}

ul.tabs > li:before{
    border-color : transparent #ddd transparent transparent;
    -webkit-transform : rotate(48deg);
    left: -23px;
}

ul.tabs > li:after{
    border-color : transparent transparent transparent #ddd;
    -webkit-transform : rotate(-48deg);
    right: -17px;
}
.tabs {
    display: flex;
    white-space: nowrap;
    transition: transform 0.3s;
}

.tab {
    white-space: nowrap;
    margin-right: 10px;
}


